<template>
  <div class="columns is-gapless">

    <div class="column is-320px">
      <transition name="page" mode="out-in">     
        <div v-if="builderIframe.isReady">
          <div class="tabs is-boxed">
            <ul>

              <router-link :to="'/websites/1/build'" tag="li">
                <a>Build</a>
              </router-link>

              <router-link to="/websites/1/settings" tag="li">
                <a>Settings</a>
              </router-link>

              <router-link to="/websites/2" tag="li">
                <a>Preview</a>
              </router-link>

              <router-link to="/websites/2"  tag="li">
                <a>Save</a>
              </router-link>
            </ul>
          </div>

          <transition name="page" mode="out-in">        
            <router-view></router-view>
          </transition>
        </div>
      </transition>
    </div>

    <div class="column">
      <div class="fixed">
        <div class="center">
          <div class="iframe">
            <iframe id="iframe" src="/#/iframe" style="width: 100%; height: 100%; background:#fff" ></iframe>
          </div>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import user from '../models/User'

export default {
  name: 'Website',

  data () {
    return {
      user: user,
      builderIframe: builder.iframe,
    }
  },

  created () {
    console.log(this.$router);
    user.get();
  },
}
</script>

<style scoped>

  .tabs {
    margin-top: 35px;
    font-size: 14px;
  }

  .tabs li:first-child {
    margin-left: 15px;
  }


  .fixed {
    height: calc(100vh - 52px);
    padding: 15px 15px 15px 0px;
    width: calc(100% - 320px);
    position: fixed;
  }

  .center {
    height: 100%; display: flex; justify-content: center;align-items: center;
  }

  .spinner-container {
    height: 100%; width: 100%; display: flex; justify-content: center;align-items: center; background: #222;
  }

  .iframe {
    border-radius: 4px;
    overflow:hidden;
    width: 100%;
    height: 100%;
  }

  .is-320px {
    -webkit-box-flex: 0;
    -ms-flex: none;
    flex: none;
    width: 320px;
  }
</style>



