import axios from 'axios';
import api from '../config/api';

export default {
  data: null,

  authenticate() {
    if (!localStorage.user) this.guest();
    
    else {
      this.data = JSON.parse(localStorage.user);
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.data.access_token;   
    }
  },

  guest() {
    let user = this;

    axios.post(api.guest())
      .then(function (response) {
        axios.defaults.headers.common['Authorization'] = 'Bearer ' + response.data.access_token;
        user.data = response.data;
        localStorage.setItem('user', JSON.stringify(response.data));
      })
      .catch(function (error) {
        console.log(error);
      });
  },

  login() {

  },

  register() {

  },

  welcomeMessage() {
    if (this.data.role == 'guest') {
      return 'Welcome Guest';
    }
    return 'Welcome ' + this.data.name;
  },

  get() {
    axios.post(api.url + '/user')
      .then(function (response) {
        // console.log(response.data);
      })
      .catch(function (error) {
        // console.log(error);
      });   
  }
}


